import wx from 'weixin-js-sdk';

let recordTimer = 0;

export default {

  mounted() {
    // this.$router.push({
    //   name: 'dataReport',
    //   params: {
    //     voice: '语音输入的文字'
    //   }
    // });
  },

  data(){
    return {
      recording: false,
      pending: false
    }
  },

  computed: {
    primaryTip() {
      if (this.pending && !this.recording) {
        return '正在识别并查询...';
      } else if (this.recording) {
        return '正在接收语音...';
      } else {
        return '点击说出指令';
      }
    },

    secondaryTip() {
      if (this.pending && !this.recording) {
        return '';
      } else if (this.recording) {
        return '点击停止说话';
      } else {
        return '通过语音查询数据报告';
      }
    }
  },

  methods: {
    // 录音开始/结束
    handleToggleRecord() {
      if (this.recording) {
        this.stopRecord();
      } else {
        // 调用微信录音接口
        wx.startRecord({
          cancel: () => {
            this.$vux.toast.show({
              type: 'warn',
              text: '未授权录音'
            });
          }
        });

        this.recording = true;
        // 10秒后自动结束录音
        recordTimer = setTimeout(() => {
          this.stopRecord();
        }, 10000);
      }
    },

    // 录音结束
    stopRecord(){
      // 取消定时操作
      clearTimeout(recordTimer);
      // 调用微信sdk结束录音
      wx.stopRecord({
        success: res => {
          const localId = res.localId;

          this.pending = true;

          wx.translateVoice({
            localId,
            isShowProgressTips: 0,
            complete: res => {
              if (res.hasOwnProperty('translateResult')) {
                this.pending = false;

                this.$router.push({
                  name: 'dataReport',
                  params: {
                    voice: res.translateResult
                  }
                });
                
              } else {
                this.pending = false;
                this.$vux.toast.show({
                  type: 'warn',
                  text: '无法识别'
                });
              }
            }
          })
        }
      });

      this.recording = false;
    }
  }
}



